.title {
    color: var(--color-main);
    margin-bottom: 18px;
    font-size: 18px;
    text-align: center;
}

.orderBox {
    width: 96%;
    margin: 0 auto;
    padding: 30px 20px;
    border: 2px solid var(--color-main);
    border-radius: 10px;
    color: var(--color-main);
    background-color: rgb(255, 255, 255, 0.75);
}

.subTitle {
    position: relative;
    margin-bottom: 20px;
    font-size: 24px;

}

.subTitle::before {
    content: "";
    width: 12px;
    height: 12px;
    border: 2px solid var(--color-main);
    border-radius: 50%;
    background-color: #ffffff;
    position: absolute;
    left: -29px;
    top: 4px
}

.locality {
    font-size: 20px;
    font-weight: 700;
    color: #000000;
    margin-bottom: 6px;
}

.localityStop {
    color: #636363;
    margin-bottom: 14px;
}

.date, .date2 {
    position: relative;
    color: #424242;
    font-weight: 700;
    margin-bottom: 40px;
}
.date2 {
    margin: 0;
}

.date::after {
    content: '';
    display: block;
    width: 100%;
    margin-top: 20px;
    border-bottom: 1px solid var(--color-main);
}

.total {
    background-color: var(--color-secondary);
    color: var(--color-main);
    font-size: 22px;
    font-weight: 900;
    line-height: 1.5;
    padding-left: 6px;
}

@media (min-width:768px) {
    .orderBox {
        width: 300px;
    }
    .sticky {
        position: sticky;
        top: 10px;
    }
}
@media (min-width:992px) {
    .orderBox {
        width: 340px;
    }
    .title {
        font-size: 24px;
    }
    
}
@media (min-width: 1200px) {
    .orderBox {
        width: 400px;
    }
} 